const addClass = function(elements, newClass) {
	// if there are no elements, end function
	if (!elements) return;

	// if we have a selector, get the chosen elements
	if(typeof(elements) === 'string') {
		elements = document.querySelectorAll(elements);
	}

	// if we have a single DOM element, make it an array to simplify behavior
	else if(elements.tagName) {
		elements=[elements];
	}

	// add class to all chosen elements
	elements.forEach((el) => {
		el.classList.add(newClass);
	});
};

const removeClass = function(elements, newClass) {
	// if there are no elements, end function
	if (!elements) return;

	// if we have a selector, get the chosen elements
	if(typeof(elements) === 'string') {
		elements = document.querySelectorAll(elements);
	}

	// if we have a single DOM element, make it an array to simplify behavior
	else if(elements.tagName) {
		elements=[elements];
	}

	// add class to all chosen elements
	elements.forEach((el) => {
		el.classList.remove(newClass);
	});
};
const formFieldFilled = function(e) {
	// field just blurred
	let field = e.srcElement;
	// value of field
	let fieldVal = field.value;
	// find parent of field ('.input')
	let parent = field.parentNode;

	if(e.type === 'focus') {
		addClass(parent, 'input--filled');
	}
	
	// check if any characters have been entered (length > 0)
	if(e.type === 'blur' && fieldVal.length === 0) {
		// add input--filled class to parent
		removeClass(parent, 'input--filled');
	}
};


/* Master Function to set event listeners calling functions above */
const formActions = function() {
	// set formFieldFilled event listener for all fancy form inputs
	let fillableInputs = document.querySelectorAll('.form--fancy input');
	fillableInputs.forEach((el) => {
		el.addEventListener('focus', formFieldFilled);	
		el.addEventListener('blur', formFieldFilled);	
	});
};
var goToTop = function() {
	var waypoint = new Waypoint({
      element: $('.navigation--primary'),
      handler: function(direction) {
        if(direction === 'down') {
            $('.go-top-button').addClass('go-top-button--visible');
        } else {
            $('.go-top-button').removeClass('go-top-button--visible');
        }
      },
      offset: -700
    });

	$('.go-top-button').on('click', function(e) {
		e.preventDefault();
		$('html, body').animate({scrollTop: 0}, 500, 'swing', function() {
			$('html').scroll();
			$('.navigation--primary').removeClass('navigation--primary-fixed');
		});
	});
};
let lazyObserver,
	imageCount;

/*****
*
* Master Functions
*
*****/

// master lazy loader function, to set up the observer && observe the appropriate images
const lazyLoad = function() {
	// get all of the images marked up to lazy load
	const images = document.querySelectorAll('.lazy-load');

	// config for the observer
	const config = {
		rootMargin: '50px 0px', // if img gets within 50px of viewport, start the download
		threshold: 0.01
	};

	// init variables
	imageCount = images.length;

	if(!('IntersectionObserver' in window)) {
		$('.lazy-load').unveil();
	} else {
		// init observer
		lazyObserver = new IntersectionObserver(onIntersection, config);
		// observer all images w/ class
		images.forEach(image => {
			if(image.classList.contains('lazy-load--handled')) {
				return;
			}

			lazyObserver.observe(image);
		});
	}
};

// handle intersections
const onIntersection = function(entries) {
	// disconnect if we've already loaded all images
	if(imageCount === 0) {
		lazyObserver.disconnect();
	}

	// loop through the entries
	entries.forEach(entry => {
		// check if in viewport
		if(entry.intersectionRatio > 0) {
			imageCount--;
			// stop watching
			lazyObserver.unobserve(entry.target);
			// load the image
			preloadImage(entry.target);
		}
	});
};

/******
*
* Image Loading Utility Functions
*
******/

// fetches the image for the given URL
const fetchImage = function(url) {
	return new Promise((resolve,reject) => {
		const image = new Image();
		image.src = url;
		image.onload = resolve;
		image.onerror = reject;
	});
};

// preloads the image
const preloadImage = function(image) {
	const src = image.dataset.src;

	if(!src) return;

	// get and load the image based on the data-src provided in markup
	return fetchImage(src).then(() => {
		applyLoadedClass(image, src);
	});
};

// load all images immediately (fallback when lacking browser support)
const loadImagesImmediately = function(images) {
	Array.from(images).forEach(image => preloadImage(image));
};

// apply the class to prevent image from being loaded a second time
function applyLoadedClass(img, src) {
	addClass(img, 'lazy-load--handled');
	img.src = src;
	addClass(img, 'fade-in');
}

/*****
*
* Observer Utilities
*
*****/

// disconnect the observer
const disconnectObserver = function() {
	if(!lazyObserver) return;

	lazyObserver.disconnect();
};
var mobileNav = function() {
	$('.navigation--mobile__trigger').on('click', function(e) {
		e.preventDefault();
        if($('.navigation--mobile__page').css('right') === '0px') {
            $('.navigation--mobile__page').css('position', 'fixed');
            $('.navigation--mobile__container').css('display', 'block');
            //$('nav li.logo').css('display', 'none');
            $('.navigation--mobile__page').animate({ right:250 }, '200', function() {
                $('.navigation--mobile__trigger').addClass('navigation--mobile__trigger--arrow');
            });
            //$('.navigation--primary').css('position', 'absolute').animate({right:250}, 'slow');
        }
        else {
            $('.navigation--mobile__page').animate({ right:0 }, '200', function() {
                $('.navigation--mobile__page').css('position', 'absolute');
            	//$('nav li.logo').css('display', 'block');
                $('.navigation--mobile__trigger').removeClass('navigation--mobile__trigger--arrow');
                $('.navigation--mobile__container').css('display', 'none');

            });
            $('.navigation--primary').animate({right:0}, '200');
        }
	});
};

var stickyNav = function() {
    var waypoint = new Waypoint({
      element: $('.navigation--primary'),
      handler: function(direction) {
        if(direction === 'down') {
            $('.navigation--primary').addClass('navigation--primary-fixed');
        } else {
            $('.navigation--primary').removeClass('navigation--primary-fixed');
        }
      },
      offset: -300
    });

    var footerHeight = $('footer').height();
    $(window).scroll(function() {
        if($(window).scrollTop() + $(window).height() >= $(document).height() - footerHeight + 200) {
            $('.navigation--primary').removeClass('navigation--primary-fixed');
        }
    });
};

function pageActions() {
	// nav stuff
	stickyNav();
	mobileNav();

	// go to top button
	goToTop();

	// form inputs, validation, submit intercept, etc.
	formActions();

	// lazy load images when the reach viewport
	lazyLoad();
}


$(document).ready(function() {
	pageActions();
});

